<template>
  <v-app>
    <v-app-bar
      app
      dark
      outlined
      clipped-right
      elevation="4"
      max-height="90px"
      :shrink-on-scroll = "!mobile"
      elevate-on-scroll
    >
      <router-link :to="{name: 'home'}">
        <v-img
          class="ml-5"
          position="left"
          height="3rem"
          :max-width="logowidth"
          contain
          src="uploads/uo-logo.jpg">
        </v-img>
      </router-link>
      <v-spacer></v-spacer>
      
      <div v-show="!mobile">
        <v-btn :color="themeColor" text>Become A Dealer</v-btn>
        <v-btn :color="themeColor" text>FAQ</v-btn>
        <v-btn :to="{name: 'products'}" :color="themeColor" text>Products</v-btn>
        <v-btn :to="{name: 'team'}" :color="themeColor" text>Team</v-btn>
        <v-btn :to="{name: 'contact'}" :color="themeColor" text>Contact</v-btn>
      </div>
      
      
      <v-app-bar-nav-icon @click="drawer=!drawer" v-show="mobile"></v-app-bar-nav-icon>
    </v-app-bar>

    <v-main>
      <router-view></router-view>
    </v-main>

    <v-navigation-drawer 
      :app="mobile"
      right
      clipped
      stateless
      v-model="drawer"
      v-show="mobile"
    >
      <nav-drawer :hideDrawer="hideDrawer"></nav-drawer>
    </v-navigation-drawer>

    <v-footer dark outlined>
      <div class="theme-text text-center mt-5 mx-auto font-weight-light pb-2">
        Copyright &copy; {{ new Date().getFullYear() }} <b>Uriri Oghene Nigeria Ltd</b>. All rights reserved.
      </div>
    </v-footer>
  </v-app>
</template>

<script>
import NavDrawer from "./components/NavDrawer";

export default {
  name: "App",
  data: () => ({
    drawer: false,
    themeColor: "#ff5722"
  }),
  components: {
    NavDrawer
  },
  computed: {
    mobile() {
      return this.$vuetify.breakpoint.smAndDown
    },
    logowidth(){
      return this.mobile ? '12rem' : '14rem'      
    }
  },
  watch: {
    mobile(newValue) {
      if (newValue) {
        this.drawer = false
      }
    }
  },
  methods: {
    hideDrawer(){
      if(this.mobile){
        this.drawer = false;
      }
    }
  }
}
</script>

<style>
  .theme-text {
    color: #ff5722;
  }
</style>